@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Text&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



#logo {
    text-align: center;
    top: 30px;
    right: 20px;
    font-family: 'Press Start 2P', cursive;
    margin-top: 50px;
    margin-bottom: 50px;
}
.topnav {
    background-color: #333;
    overflow: hidden;
}
.topnav #logo {
    color: #ffffff;
}
.topnav li {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
    list-style: none;
}
.topnav li:hover {
    background-color: #ddd;
    color: black;
    cursor:pointer;
}
.topnav li.active {
    background-color: #4caf50;
    color: white;
}
.github{
    margin: 0;
    padding: 0;
}
.topnav a{
   float: right;
    margin: 0;
    padding: 0;
}
.topnav img{
    height: 50px;
}
.search {
    margin: 30px;
    text-align: center;
  }
  .search_box {
    width: 250px;
    height: 35px;
    border-radius: 5px;
  }
  .search_box:hover{
    box-shadow: 1px 1px 1px 1px gray;
  }
  .search_btn {
    width: 60px;
    height: 35px;
    text-align: center;
  }
.versions {
    border : 0;
    display: inline;
    list-style:none;
}
.category{
    list-style:none;
    font-size: 20px;
}

b:hover{
    cursor: pointer;
    text-decoration: underline;
}
b+ul:focus{
    display: none;
}
b{
    font-size: 15px;
}
.category{
    font: 20px;
}
#instruction{
    padding-left: 10px;
}
table {
    margin: auto;
    top: 90px;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 50%;
    place-content: center;
}

td,
th {
    border: 1px solid #ddd;
    text-align: left;
}

th {
    background-color: #333333;
    color: #ffffff;
    text-align: center;

}

tr {
    background-color: #f2f2f2;
}

tr:nth-child(even) {
    border: 1px solid #ddd;
    background-color: #ddd;
}

.resultImg {
    width: 25%;
}

td {
    text-align: center;
    width: 70%
}
h2{
    text-align: center;
}
#recommend{
    text-align: center;
}
.recommendBtn{
    background-color: black;
    border-radius: 10px;
    padding: 10px;
    color: white;
    font-size: medium;
}
.recommendBtn:hover{
    background-color: #eff3f7;
    color: black;
    cursor:pointer;
}
footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 75px;
    background-color: #333;
    color: white;
    padding: 15px 3px 3px;
}

#copyright{
    text-align: center;
}
a{
    text-decoration: none;
    color: white;
}
.main {
    display: block;
}
* {
    box-sizing: border-box;
}
.contents-wrap {
    min-height:100%;
    position:relative;
    padding-bottom:80px;/* footer height */
}
body {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;

}
